import { countdown } from "../../store/index";
import { useSetRecoilState } from "recoil";
import { motion } from "framer-motion";
import "./index.css";

export default function Modal(props) {
  const variants = {
    hidden: { opacity: 0, scale: 0 },
    visible: {
      opacity: 1,
      scale: 1,
      transition: {
        duration: 0.6,
      },
    },
  };
  const player = props.player;
  const setCpunt = useSetRecoilState(countdown);
  const handleClose = () => {
    setCpunt(15);
  };
  return (
    <motion.div
      initial="hidden"
      animate="visible"
      variants={variants}
      className="modal-container"
    >
      <div className="modal-content">
        <img className="modal_xiaoxin" alt="xiaoxin" src="xiaoxin_win.gif" />
        <p className="modal-title">注意</p>
        <span className="modal-winner">{player === 1 ? "⚪" : "⚫"}</span>
        <p className="modal-desc">你思考时间太长了哦！</p>
        <span className="modal-close" onClick={handleClose}>
          ❌
        </span>
      </div>
    </motion.div>
  );
}
